@media only screen and (max-width: 600px){.main__description--title{font-size:30px}.footer{-webkit-box-align:start;align-items:flex-start;-webkit-box-orient:vertical;-webkit-box-direction:normal;flex-direction:column}}.register{background-image:-webkit-gradient(linear, left top, left bottom, from(#21c08b), to(#8f57fd));background-image:linear-gradient(#21c08b, #8f57fd);display:-webkit-box;display:flex;-webkit-box-align:center;align-items:center;-webkit-box-orient:vertical;-webkit-box-direction:normal;flex-direction:column;-webkit-box-pack:center;justify-content:center;padding:0px 30px;min-height:calc(100vh - 200px)}.register__container{background-color:rgba(255,255,255,0.1);border:2px solid white;border-radius:40px;box-shadow:0 14px 28px rgba(0,0,0,0.25),0 10px 10px rgba(0,0,0,0.22);color:white;display:-webkit-box;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;flex-direction:column;justify-content:space-around;padding:60px 68px 40px}.register__container--form{display:-webkit-box;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;flex-direction:column}.register__container a{color:white;font-size:16px;text-align:center;text-decoration:none}.register__container a:hover{text-decoration:underline}
