@media only screen and (max-width: 600px){.main__description--title{font-size:30px}.footer{-webkit-box-align:start;align-items:flex-start;-webkit-box-orient:vertical;-webkit-box-direction:normal;flex-direction:column}}.carousel{overflow:scroll;padding-left:30px;width:100%;position:relative}.carousel__container{transition:450ms -webkit-transform;-webkit-transition:450ms -webkit-transform;transition:450ms transform;transition:450ms transform, 450ms -webkit-transform;font-size:0;white-space:nowrap;margin:70px 0px;padding-bottom:10px}.carousel__container:hover .carousel-item{opacity:0.3}.carousel__container:hover .carousel-item:hover{-webkit-transform:scale(1.5);transform:scale(1.5);opacity:1}
